.header {
  background-color: #242423;
  position: relative;

  &__section {
    @include flex-align-center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    //height: 140px;

  }

  &__search {
    order: 3;
    @include flex-align-center;
    justify-content: space-around;
    align-self: center;
    gap: 10px;
    width: 100%;
    padding: 5px 15px;
    margin: 0 auto;
    background-color: rgb(40, 40, 44);

    &-input {
      border: none;
      padding: 5px;
      width: 90%;
      background-color: transparent;
      outline: none;
      color: #FFF;
      @include font-style(16px, 16px, normal);
    }

    &-btn {
      color: #f5cb5c;
    }

    &-btn:hover,
    &-btn:focus {
      color: #e8eddf;
    }
  }

  &__navbar {
    position: absolute;
    top: 123px;
    left: 0;
    width: 100%;
    display: none;
    background-color: rgb(40, 40, 44);
  }

  &__navbar.active {
    display: block;
  }
}

.logo__img {
  transition: width 0.5s ease-in-out;
}

.menu__toggle {
  order: 2;

  &:hover,
  &:focus {
    fill: #f5cb5c;
  }
}

.movie__categories {
  @include flex-align-center;
  flex-direction: column;
  justify-content: center;
}

.categories {
  &__item {
    width: 100%;
    padding: 20px;
    border: 1px solid #2c2c2c;
    @include font-style(16px, 16px, normal);

    &:hover {
      color: #f5cb5c;
    }
  }
}

@media (min-width: 460px) {
  .menu__toggle {
    order: 3;
  }

  .header__search {
    width: auto;
    order: 2;
  }

  .logo__img {
    width: 75px;
    height: 75px;
  }

  .categories__item {
    @include font-style(18px, 18px, normal);
  }
}

@media (min-width: 860px) {
  .logo__img {

    width: 100px;
    height: 100px;
  }
}